@use '../../scss/_variables' as vars;

.card-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;

    .cards{
        background-color: vars.$cardNavBgColor;
        margin: 1rem;
        padding: 1rem;
        border-radius: 10px;
        box-shadow: 3px 3px 5px rgba(0,0,0,0.7);

        /* card'ın dışına sarkan matnin gizlenmesi */
        overflow: hidden;

        
        /* metnin position'ını altta ayarlamak için.. */
        position: relative;

        /* metin yukarı çıksın, başka elementin durumunu değiştirme */
        &:hover .card-over{
            transform: translateY(0%);
        }
    }

    .title{
        color: vars.$backgroundColor;
        font-size: 1.5rem;
        padding: 1rem;
    }
    
    img {
        width: 500px;
        height: 350px;
        object-fit: cover;
        
        &:hover{
            filter: grayscale(100%)
        }
    }

    .card-over {
        color: white;

        /* metnin position'ını altta ayarlamak için.. */
        position: absolute;
        bottom: 0;
        left: 0;

        background-color: rgba(0,0,0,0.5);
        font-size: 1.5rem;
        padding: 1rem;

        transform: translateY(100%);
        z-index: 1;
        transition: transform 0.7s ease-in-out;
    }

}

